<template>
  <!-- TODO: Usar tag 'nav' na nav -->
  <ul :class="ulClassList">
    <li @click="scrollBehaviorToId('15dfb042-home')">Home</li>
    <li @click="scrollBehaviorToId('15dfb042-about-us-section')">Sobre</li>
    <li @click="scrollBehaviorToId('15dfb042-solution-section')">Soluções</li>
    <li @click="scrollBehaviorToId('15dfb042-our-steps-section')">Nossos Passos</li>
    <li @click="scrollBehaviorToId('15dfb042-hire-us-section')">Contrate-nos</li>
  </ul>
</template>

<script>
export default {
  name: 'Nav',
  props: {
    withBulletSeparator: Boolean,
    underline: Boolean,
  },
  computed: {
    ulClassList: function() {
      return {
        'nav--underline': this.underline,
        'nav--with-bullet-separator': this.withBulletSeparator,
      };
    },
  },
  methods: {
    scrollBehaviorToId(id) {
      const el = document.getElementById(id);
      if (el) {
        el.scrollIntoView({ behavior: 'smooth' });
      } else {
        console.warn(`Não foi encontrado um elemento com id "${id}"`);
      }
    },
  }
}
</script>

<style scoped>
ul {
  display: flex;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  text-transform: uppercase;
  color: white;
  list-style: none;
  letter-spacing: 0.05em;
}

ul li {
  cursor: pointer;
  padding-bottom: 7px;
}

ul:not(.nav--with-bullet-separator) {
  column-gap: 15px;
}

ul:not(.nav--with-bullet-separator) li {
  border-bottom: 2px solid transparent;
  transition: border-color 300ms ease;
}

ul:not(.nav--with-bullet-separator) li:hover {
  border-bottom-color: var(--secondary-color);
}

ul.nav--underline li {
  text-decoration: underline;
}

ul.nav--with-bullet-separator li:hover {
  color: var(--secondary-color);
}

ul.nav--with-bullet-separator :not(li:last-child)::after {
  content: "•";
  display: inline-block;
  padding: 0px 0.5rem;
}
</style>
